import React, { useState } from 'react';
import { Link } from "react-router-dom";

import "../css/header.css";

const spacing = {
	margin: '0 0.3rem',
};

const HeaderLinks = [
{
	Name: "Home",
	Url: "/",
},
{
	Name: "Experiences",
	Url: "/experiences",
},
{
	Name: "Projects",
	Url: "/projects",
},
];

const HeaderLeft = () => (
	<div className="flex_group">
		<div>
		<Link className="logo-link" to="/">Jeff Zhao</Link>
		</div>
		<div style={spacing}/>
		<NonHamburgerMenu/>
	</div>
);

const NonHamburgerMenu = () => (
	<div className="non-hamburger-menu">
	{HeaderLinks.map(function(link, index) {
		return (
	<Link className="header-link" key={index} to={link.Url}>{link.Name}</Link>
		);
	})
	}
	</div>
);

function HamburgerMenu(props) {
	const [ visible, setVisible ] = useState(false);

	async function handleVisible(event) {
		event.preventDefault();
		setVisible(!visible);
		console.log(visible);
	}

	if (!visible) {
		return (
		<div className="hamburger-menu" onClick={e => handleVisible(e)}>
			<div className="bar1"></div>
			<div className="bar2"></div>
			<div className="bar3"></div>
		</div>
		);
	}

	return (
	<div>
	<div className="hamburger-button change" onClick={e => handleVisible(e)}>
		<div className="bar1"></div>
		<div className="bar2"></div>
		<div className="bar3"></div>
	</div>
	<div className="hamburger-menu">
		<ul>
		{HeaderLinks.map(function(link, index) {
			return (
		<li><Link className="header-link" key={index} to={link.Url}>{link.Name}</Link></li>
			);
		})
		}
		</ul>
	</div>
	</div>
	);
}

function Header(props) {
	return (
<nav className="topnav">
	<HeaderLeft/>
	<div className="flex_group">
		<HamburgerMenu/>
	</div>
</nav>
	);
}

export default Header;
export { Header };
